// @import "compass/css3";
$scroll:10px;
::-webkit-scrollbar{
   width:$scroll;
}
::-webkit-scrollbar-thumb{
   border-radius:$scroll;
   background-color:rgba(255,255,255,0.2);
}
::-webkit-scrollbar-track{
   border-radius:$scroll;
   background-color:rgba(255,255,255,0.1);
}
body{
   font-size:100%;
   background-image:linear-gradient(top left,#333,black);
   background-attachment:fixed;
}
*,*:before,*:after{
   box-sizing:border-box;
}
ul,li{
   list-style-type:none;
}
.wrapper{
   background-color: #272328;
   padding-top:80px;
   margin-top: 20px;
   border-radius: 30px;
}
.wrapper ul{
   width: 100%;
   margin:0 auto;
   height:300px;
   overflow-x:hidden;
   overflow-y:scroll;
   padding-right:30px;
}
.wrapper ul li{
   margin-bottom:20px;
   padding:10px 20px;
   background-color:rgba(255,255,255,0.65);
   position:relative;
   border-radius:10px;
   font:1em/1.6 "arial";
   clear:both;
   text-shadow:1px 1px 0 rgba(255,255,255,0.35);
}
.wrapper ul .chat-li-left{
  background-color:#575757;
  color:white;
   float:left;
   margin-left:80px;
   max-width: 60%;
   font-size: 16px;
}
.wrapper ul .chat-li-right{
   float:right;
   margin-right:80px;
   color:black;
   font-size: 16px;
   background-color:#a5cdd3;
}
.wrapper ul li:before,.wrapper ul li:after{
   position:absolute;
   content:"";
}
.wrapper ul li:before{
   width:45px;
   height:45px;
   top:0;
   border-radius:50%;
   background-position:50% 50%;
   background-repeat:no-repeat;
}
.wrapper ul li:nth-child(odd):before{left:-80px;background-image:url("data:image/jpeg;base64,");}
.wrapper ul li:nth-child(even):before{right:-80px;background-image:url("data:image/jpeg;base64,");}

.wrapper ul li:after{
   width:0;
   height:0;
   top:15px;
   border-top:15px solid rgba(255,255,255,0.65);
}
.wrapper ul li:nth-child(odd):after{
   left:-15px;
   border-left:15px solid transparent;
}
.wrapper ul li:nth-child(even):after{
   right:-15px;
   border-top-color:rgba(255,255,255,0.3);
   border-right:15px solid transparent;
}
@media only screen and (min-width:768px){
    .wrapper ul{
       width:100%;
       height:80vh;
   }
}
@media only screen and (max-width:767px){
    .wrapper ul{
       width:90%;
       height:80vh;
   }
}

.input-row{
  padding: 10px 20px  20px  0px ;
}
.mesage-text{
  width: 100%;
  background-color: #3d3d3d;
  border-radius: 20px;
  height: 60px;
  border: none;
  padding: 20px;
  font-size: 16px;
  color: white;
}
.audio-button{
  background-color: #c0bdbc;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  color: black;
  font-size: 22px;
  display: flex;
  justify-content: center;
  padding-top: 5px;

}
.align-center{
  display: flex;
  justify-content: center;
}